.nav{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    text-align: center;

    background-color: var(--main-background-color);
    padding: 12px 16px;

    height: 94px;
    max-width: 480px;
    margin: 0 auto;
}

.navTitle{
    color: var(--secondary-brand-color);
    font-size: 24px;
    font-weight: bold;
    letter-spacing: 0.1em;
}

.navIconWrapper{
    display: flex;
    justify-content: center;
    align-items: center;
}

.navIcon{
    height: 32px;
    width: 32px;
    margin-right: 8px;
}

.categories{
    display: flex;

    width: 100%;
    padding: 16px 0 0 0;

    overflow-x: scroll;
    overflow-y: hidden;

    /* hide scroll bar from IE, edge and firefox */
    -ms-overflow-style: none; /*IE and edge*/
    scrollbar-width: none; /*firefor*/
    
}
/* Hide Scrollbar for chrome, safari, and opera */
.categories::-webkit-scrollbar{
    display: none;
}

.categories > a{
    margin: 0 4px;
}

.category{
    padding: 4px 8px;
    background-color: var(--secondary-brand-color);
    border-radius: 4px;
    color: var(--secondary-font-color);
    font-size: 10px;
    font-weight: 600;

    text-decoration: none;
}

.selected {
    background-color: var(--main-font-color);
}